const state = {
  securityKeyName: null,
  userTokens: null,
  apiVersion: null
}

const getters = {
  securityKeyName: state => {
    return state.securityKeyName
  },
  userTokens: state => {
    return state.userTokens
  },
  apiVersion: state => {
    return state.apiVersion
  }
}

const mutations = {
  securityKeyName: (state, name) => {
    state.securityKeyName = name
  },
  userTokens: (state, tokens) => {
    state.userTokens = tokens
  },
  apiVersion: (state, version) => {
    state.apiVersion = version
  }
}

const actions = {
}

export default {
  state,
  getters,
  mutations,
  actions
}



