import ApiResource from './components/resource/Resource'
import ApiIntroduction from './components/introduction/Introduction'
import apiDocService from './api-doc-service'
import apiDocVersionsService from './api-doc-versions-service'
import ApiNavigation from './components/navigation/Navigation'
import ApiAction from './components/action/Action'
import VueScrollTo from 'vue-scrollto'
import tokenService from '@/pages/home/tabs/tokens/token-service'
import utils from '@/support/utils'

export default {
  data: () => ({
    loaded: false,
    apiDoc: null,
    showInteractiveWindow: false,
    selectedAction: null,
    selectedResource: null,
    firstViewUpdate: true,
    baseUrl: null,
    apiVersion: null,
    apiVersions: null,
    actionOpen: false,
    resourcesHeight: null,
    apiResourcesContainerId: 'api-resources-container',
    actionContainerClass: 'api-action'
  }),
  created () {
    // emit the an event catch by root App component
    // telling it to update the page title
    this.baseUrl = this.$route.meta.baseUrl
    this.setPageTitle()
    this.showInfo(this.$t('examples.loadingDocs'))

    // Before loading the api versions and the api doc, we get the user's token
    // this is necessary to be able to pass the user tokens to the api doc adapter
    // so it can replace the ap-key field input text by a select with the user's tokens.
    // If no user is logged in or if the user has no valid token, then the api doc adapter
    // will keep the api-key field as default (a text input)
    this.getUserTokens().then((tokens) => {
      // Save the user's api key param name in the vuex store to be used by the apiDocService parser
      this.$store.commit('userTokens', tokens)

      // We load the api doc versions
      // then we pre-select the first version on the list as
      // the selected one, then we load the api doc
      apiDocVersionsService.query().then((versions) => {
        if (versions.length === 0) {
          this.showError(this.$t('examples.errorWhileLoadingApiVersions'))
        } else {
          this.apiVersions = versions
          // by default the latest version is selected
          // The change of value of apiVersion will automatically
          // trigger the this.loadApiDoc() method.
          this.apiVersion = this.apiVersions[this.apiVersions.length - 1]

          // Save the current version selected in a store
          // Because if can be used by other components
          this.$store.commit('apiVersion', this.apiVersion)
        }
      })
    })

    this.eventBus.$on('apiCallEnd', () => {
      this.updateResourcesListingHeight()
    })
  },
  updated () {
    // When a user access directly a page that points to a specific
    // resource or action we need to load the corresponding resource
    // and action and scroll the page to the target resource/action
    // But, we only want to trigger the auto scroll if
    // it is the first view update (that occurs after page is loaded, data is acquired and view is rendered for the first time)
    // In the subsequent view updates we will manage the scroll in the element's click event
    if (this.firstViewUpdate) {
      this.firstViewUpdate = false
      this.setActionAndResourceByRoute()
      this.scrollToRoute()
    }
    // Every time the view is updated, we need to reset the height of
    // the resources listing container, so we keep both
    // side by side containers with the same heigh
    this.updateResourcesListingHeight()
  },
  watch: {
    /**
     * Every time the route change, we have to run the scroll
     * to make sure the page content is focused/scrolled to the current route content
     * @param {*} to
     * @param {*} from
     */
    '$route' (to, from) {
      this.setActionAndResourceByRoute()
      this.setPageTitle()
      this.scrollToRoute()
    },

    /**
     * Reload the API doc file every time the selected API version changes
     */
    'apiVersion' () {
      this.loadApiDoc()
    }
  },
  methods: {
    /**
     * When the a navigation's component item is clicked, the event is delegated to this method.
     * Here we build the target path and go the the target route
     */
    navigateTo (item) {
      let path = this.baseUrl + item.anchor
      this.$router.push({path: path})
    },

    /**
     * Scroll to the current route corresponding element on the view
     * This is intended to be used after the route is changed
     */
    scrollToRoute () {
      // Get the resource id based on the route
      let resourceElementId = this.getApiResourceElementId(this.$route)

      // check if we have a valid resource id
      if (resourceElementId !== '') {
        // if there is no action selected
        // we just scroll to the resource element
        if (!this.selectedAction) {
          let containerSelector = this.splitHorizontally ? `#${this.apiResourcesContainerId}` : 'body'
          this.scrollToElement(`#${resourceElementId}`, containerSelector)
        } else {
          // If an action is selected and the interface is NOT
          // spitted horizontally (when in low resolution)
          // just scroll the whole body to the action container
          if (!this.splitHorizontally) {
            let context = this
            setTimeout(() => {
              context.scrollToElement(`.${context.actionContainerClass}`, 'body')
            }, 500)
          } else {
            // In this case we have an horizontally spitted interface
            // so we initially scroll the whole page to the top
            window.scrollTo({top: 0, left: 0, behavior: 'smooth'})

            // Then wait half second o wait the scrolling smoothness
            // and scroll the left side of the interface to the resource
            let context = this
            setTimeout(() => {
              context.scrollToElement(`#${resourceElementId}`, `#${context.apiResourcesContainerId}`)
            }, 500)
          }
        }
      }
    },

    /**
     * Get the resource element id of a given route
     * @param {*} route
     * @return {String} apiResourceElementId
     */
    getApiResourceElementId (route) {
      let apiResourceElementId = ''
      // we iterate over the route params to remount the element key/id using the same pattern
      // applied at @/pages/api-docs/api-doc-service.js:parseResources
      for (var prop in route.params) {
        if (!route.params.hasOwnProperty(prop)) continue
        if (route.params[prop]) {
          apiResourceElementId += '_' + utils.getValidId(route.params[prop])
        }
      }

      return apiResourceElementId
    },

    /**
     * Set the pge title according the route about to be loaded
     */
    setPageTitle () {
      // Set the default title
      let title = this.$t('examples.pageTitle')

      // If we are in a sub page, set the dynamic title
      if (this.$route.path !== this.baseUrl) {
        let routeParts = this.$route.path.replace(this.baseUrl, '').split('/').filter(part => part.length > 0)

        // Create the subtitle by joining al the path segments using ` > `
        // and transforming each segment first letter in a capital letter
        let subtitle = routeParts.join(' > ').replace(/\b\w/g, l => l.toUpperCase())

        // Add the site title as an append to every generated page title
        title = subtitle + this.$t('examples.resourcePageTitleAppend')
      }
      // Emit an event to update the webapp page title
      this.eventBus.$emit('titleChanged', title)
    },

    /**
     * We set the action container height every time the view changes.
     * This height property is consumed by the `apiListingStyle` computed property
     * that returns the max height that the resources container must have.
     *
     * Doing this, both side by side containers will always have the same height
     * and the resources container will have an auto scroll
     */
    updateResourcesListingHeight () {
      // we start considering the action height as 0, because maybe there is no action component visible
      let actionHeight = 0
      // we know that in our view, considering the menu and footer, the useful height is the following
      let usefulHeight = window.innerHeight - 300

      // we try to get the action component height
      let actionContainer = this.$el.querySelector(`.${this.actionContainerClass}`)
      if (actionContainer) {
        actionHeight = actionContainer.offsetHeight
      }
      // we select the greater height, whe useful or the action height
      this.resourcesHeight = Math.max(actionHeight, usefulHeight)
    },

    /**
     * Load the API doc file that wil be used to build the view
     */
    loadApiDoc () {
      this.loaded = false
      apiDocService.get(this.apiVersion).then((apiJsonDoc) => {
        this.apiDoc = apiJsonDoc
        this.loaded = true
        this.setActionAndResourceByRoute()

        if (this.$store.getters.isAuthenticated) {
          if (this.$store.getters.userTokens) {
            this.showSuccess(this.$t('examples.docsReady'))
          } else {
            this.showInfo(this.$t('examples.youHaveNoValidTokens'), {mode: 'multi-line', timeout: 10000})
          }
        } else {
          // If not authenticated tell the user that s/he can login to auto fill the token key
          this.showInfo(this.$t('examples.loginToAutoFillTheAPIKey'), {mode: 'multi-line', timeout: 10000})
        }
      },
      error => {
        if (error.constructor && error.constructor.name === 'ApiDocParseException') {
          // The API Doc file retrieved from the service is invalid/malformed
          this.showError(this.$t('examples.apiDocParseError'))
        } else {
          // Could not load the API Doc file from service
          this.showError(this.$t('examples.apiDocLoadError'))
        }
      })
    },

    /**
     * Update the current API version when the user changes it
     * @param {*} version
     */
    apiVersionChanged (version) {
      this.apiVersion = version
    },

    /**
     * Handles the event when an action is closed and update the corresponding property
     * that store this info and reset the selected action
     */
    actionClosed () {
      this.actionOpen = false
      this.selectedAction = null
    },

    /**
     * Open an action, defining a new path to the router, that will  trigger a view /scroll update
     * @param {*} param
     */
    openAction (param) {
      this.actionOpen = true
      let actionPath = this.baseUrl + param.action.path
      this.$router.push({path: actionPath})
    },

    /**
     * If a user is authenticated, resolves with an array of valid user's tokens.
     * In the user is not authenticated ot s/he has not token the promise resolve with `null`
     * @return Promise - that will resolves with Array|null
     */
    getUserTokens () {
      return new Promise((resolve) => {
        if (this.$store.getters.isAuthenticated) {
          tokenService.query().then((tokens) => {
            // If the `tokens` is an empty array, we define it as `null`
            let usableTokens = null

            if (tokens) {
              // Apply a filter to get only the valid tokens from the user
              let validTokens = this.lodash.filter(tokens, function (t) { return t.is_valid })

              // Get an array containing only the token's keys from the valid tokens list
              usableTokens = this.lodash.map(validTokens, 'key')
            }
            // Check if the usableTokens has a list. If not, assign null
            usableTokens = Array.isArray(usableTokens) && usableTokens.length > 0 ? usableTokens : null

            // REsolve the promise withe the result
            resolve(usableTokens)
          }, (error) => {
            console.log(error)
            resolve(null)
          })
        } else {
          resolve(null)
        }
      })
    },

    /**
     * Scroll the a given element over the given container selector (html node name, class or id)
     * @param {*} scrollToId
     * @param {*} containerSelector(html tag name, class or id. Example: `body`, `.my-class`, `#my-id`
     */
    scrollToElement (elementSelector, containerSelector) {
      if (elementSelector) {
        let targetContainer = window.document.querySelector(containerSelector)
        if (targetContainer) {
          var options = {
            container: containerSelector,
            easing: 'ease-in',
            offset: -100,
            x: false,
            y: true
          }

          VueScrollTo.scrollTo(elementSelector, 2000, options)
        }
      }
    },
    /**
     * Set the selected action and resource based in the current route
     * @param {*} context
     */
    setActionAndResourceByRoute () {
      this.selectedAction = null
      this.selectedResource = null
      // we extract the path based in the current route and removing the base url and the final and starting slashes
      let path = this.$route.path.replace(this.baseUrl, '').replace(/^\/|\/$/g, '')

      // we expect to get as result a `null` or an object containing the properties `action` and `resource`
      let matched = apiDocService.getAdapter(this.apiVersion).getActionAndResourceByPath(path, this.resources)
      if (matched) { // if is not null
        this.selectedResource = matched.resource
        this.selectedAction = matched.action
        this.actionOpen = this.selectedAction !== null
      } else {
        this.actionOpen = false
      }
    }
  },
  computed: {
    /**
     * return the resources available in the APIDoc file
     */
    resources () {
      if (this.loaded) {
        return this.apiDoc.resources
      }
    },

    /**
     * return the API definitions available in the APIDoc file
     */
    apiDefinitions () {
      if (this.loaded) {
        return this.apiDoc.apiDefinitions
      }
    },

    /**
     * Return the selected action, if one action is selected
     */
    action () {
      return this.selectedAction
    },

    /**
     * Return the selected resource, if one resource is selected
     */
    resource () {
      return this.selectedResource
    },

    /**
     * return the inline style that must be applied to the api resources listing container
     */
    apiListingStyle () {
      if (this.splitHorizontally && this.resourcesHeight) {
        let styl = {'max-height': this.resourcesHeight + 'px'}
        return styl
      }
    },

    /**
     * Return a boolean determining if the view must be split horizontally or not
     * based in the screen resolution/breakpoint and if an action container is open
     */
    splitHorizontally () {
      return this.$vuetify.breakpoint.mdAndUp && this.actionOpen
    },

    /**
     * Return the column breakpoint that must be applied to the api resources listing container
     * we need this to be dynamically, because if the action container is visible, we use only one part
     * and split the available space with the other container, if not, we use all
     */
    listingColumnBreakPoint () {
      return this.actionOpen ? 'md5 lg5' : 'md12 lg12'
    }
  },
  components: {
    ApiResource,
    ApiIntroduction,
    ApiNavigation,
    ApiAction
  }
}



