import VueMarkdown from 'vue-markdown'

export default {
  props: {
    resource: {
      required: true
    },
    apiDefinitions: {
      required: true
    }
  },
  methods: {
    actionClick (action, resource) {
      this.$emit('actionClick', {action: action, resource: resource})
    }
  },
  components: {
    'vue-markdown': VueMarkdown
  }
}



