export default {
  path: '/reference/:path1?/:path2?/:path3?',
  name: 'OldAPIRedirection',
  beforeEnter: (to, from, next) => {
    let pathParts = to.path.split('/')
    if (pathParts.length > 0) {
      next('/api-docs/' + pathParts[2])
    }
  }
}



