/**
 *
 * @param {*} body object that represents a response sample defined in the swagger file - the example body from the response.example
 * @param String description - the description from the response.example
 * @param {integer} nesting - indentation level for recursive calls
 * @returns {bodyResponseSampleObj: {*}, responseDescription: String}
 */
const build = (bodyResponseSampleObj, responseDescription, nesting = 0) => {
  // Create a new object via assign because we don want to modify the original object
  let customBodyResponseSampleObj = Object.assign({}, bodyResponseSampleObj)

  // Go through body and write descriptions of keys with their values to the description
  // and remove unused keys/values so the leftover body is a valid response
  for (let key in customBodyResponseSampleObj) {
    if (customBodyResponseSampleObj[key]) {
      responseDescription = buildPropResponseData(customBodyResponseSampleObj, key, nesting, responseDescription)
    }
  }
  return {bodyResponseSampleObj: customBodyResponseSampleObj, responseDescription: responseDescription}
}

/**
 * Build the response sample data for a certain prop (identified by the key in the bodyResponseSampleObj)
 * considering the current nesting level and using the already built responseDescription.
 * Internally the prop in the bodyResponseSampleObj key is modified
 * @param {*} bodyResponseSampleObj
 * @param String key
 * @param {Integer} nesting level
 * @param String responseDescription
 * @returns String responseDescription
 */
const buildPropResponseData = (bodyResponseSampleObj, key, nesting, responseDescription) => {
  let prop = bodyResponseSampleObj[key]
  let type = getPropTypeDesc(prop)
  // add a indentation level to the markdown string
  let nestingLevel = '\t'.repeat(nesting)

  // Here we add to the description response the
  // markdown that represents the property item in
  // an unordered list with bullet
  // Example of output:  - __`distances`*([[double]])*__
  responseDescription += `${nestingLevel}- __\`${key}\`*(${type})*__
`
  // add the item description to the markdown string with a tab indentation
  if (prop.description) {
    responseDescription += `${nestingLevel}  : ${prop.description}
`
  }

  // Build the data for a prop that is of type object or array, defining returning an object
  // containing the new responseDescription markdown string and the modified prop object
  // without key/values that are not parte of the response sample and are appended to the
  // responseDescription markdown string
  let data = buildObjectAndArrayData(prop, responseDescription, nesting, type)
  responseDescription = data.responseDescription
  prop = data.prop

  // Get a modified version of the prop
  // with the example value built
  bodyResponseSampleObj[key] = getPropWithExampleData(prop, type)
  return responseDescription
}

/**
 * Parse a prop that is of type object or array, defining a new responseDescription
 * markdown string using as base the already built responseDescription and
 * defining a new object without props/values that should not be part of the sample response
 * object that is returned by the API. It is returned an
 * object which contains the modified prop object  and the modified responseDescription
 * built after appending data form this passed prop
 *
 * @param {*} prop the object that must be used as source to modify the responseDescription markdown string and the prop object returned
 * @param {*} responseDescription the markdown string built until this step
 * @param {*} nesting the nesting level (that represents the indentation level in the markdown)
 * @param {*} type the type description output in the markdown that describes the response of the prop object
 * @returns {prop: {*}, responseDescription: String}
 */
const buildObjectAndArrayData = (prop, responseDescription, nestingLevel, type) => {
  // If it is an object, then we get its sub properties from the properties key
  if (type === 'object' && prop.properties) {
    let data = buildNestingChildrenData(nestingLevel, responseDescription, prop.properties)
    responseDescription = data.responseDescription
    prop = data.bodyResponseSampleObj
    // If it is an array of objects, then we get its sub properties inside the item property
  } else if (type === '[object]' && prop.items && prop.items.properties) {
    let data = buildNestingChildrenData(nestingLevel, responseDescription, prop.items.properties)
    responseDescription = data.responseDescription
  }
  return {prop, responseDescription}
}

/**
 * Parse children data use to build the markdown
 * @param {*} nesting
 * @param {*} responseDescription
 * @param {*} properties
 * @returns {bodyResponseSampleObj: {*}, responseDescription: String }
 */
const buildNestingChildrenData = (nestingLevel, responseDescription, properties) => {
  nestingLevel++
  let data = build(properties, responseDescription, nestingLevel)
  nestingLevel--
  return data
}

/**
 * Get the example for the given property of the response object
 * @param {*} prop
 * @param String type
 * @returns {*}
 */
const getPropWithExampleData = (prop, type) => {
  // The example in  swagger file structure
  // can be defined with example or examples
  let example = prop.examples || prop.example

  if (example) {
    // If the string that describe the response type prop is an array
    // then it starts with `[`. If the example is an array, then
    // we need to get the first array prop
    prop = !type.startsWith('[') ? example : example[0]
  } else if (prop.properties && type === 'object') {
    prop = prop.properties
  }
  return prop
}

/**
 *
 * @param prop
 * @param arrayCall - if the function is called while checking an array, don't add array to type string
 * @returns String type
 */
const getPropTypeDesc = (prop) => {
  let type = '?'
  if (prop.type) {
    // Swagger file structure keeps the type of number in a sub prop called `format`
    type = prop.type === 'number' ? prop.format : prop.type

    // If the type of prop response is an array we also add the
    // type of the nested items to make it clear what will be returned
    if (type.startsWith('array') || type.startsWith('[')) {
      type = getFormattedArrayTypeDesc(prop)
    }
  }
  return type
}

/**
 * Get the formatted type desc in case an array
 * @param array prop of type array that is part of the response object
 * @param type initial prop response type extracted
 * @returns String type
 */
const getFormattedArrayTypeDesc = (array) => {
  let type = '['
  if (array.items) {
    type += getPropTypeDesc(array.items)
  }
  type += ']' // here we close the array type

  // Some possible outputs are:
  // [object]
  // [double]
  // [[double]]
  return type
}

export default {
  build
}



