import VueMarkdown from 'vue-markdown'
import ResponseSampleBuilder from '@/pages/api-docs/services/response-sample-builder'

export default {
  props: {
    actionData: {
      required: true
    },
    apiDefinitions: {
      required: true
    }
  },
  methods: {
    showResponseDetails (statusCode, response) {
      let details
      // if an additional json object is provided it will be displayed beyond the infoText
      let additionalJsonToBeRendered = null
      let statusCodeTitle = statusCode + ' ' + response.example.title

      // If the request has an example, let's use this
      if (response.example) {
        details = response.example.description
        // if the example provides a body, we also use it
        if (response.example.body) {
          let data = {}
          let valid = statusCode.toString() === '200'
          if (valid) {
            data = ResponseSampleBuilder.build(response.example.body, response.example.description)
          }
          additionalJsonToBeRendered = valid ? data.bodyResponseSampleObj : response.example.body
          details = valid ? data.responseDescription : response.example.description
        }
      } else {
        details = response.description
      }
      // Show info dialog
      this.infoDialog(statusCodeTitle, details, {markdown: true, resizable: true, code: additionalJsonToBeRendered})
    }
  },
  components: {
    VueMarkdown
  }
}



