/**
 * ActionRunner component.
 * It rus the call to the API according the data
 * passed via the event bus.
 * @listens callAction - event that tell this component to call a resource. Expects an object containing the apiDefinitions and actionData properties
 * @listens actionReset - reset call response to null and hide the response component
 * @emits apiCallEnd [to-parent] - when the api call finish (in case of fail or success)
 * @emits apiCallEnd [global event via eventBus]- when the api call finish (in case of fail or success)
 */
import VueMarkdown from 'vue-markdown'
import HttpOrsApi from '@/pages/api-docs/services/http-ors-api'
import JsonTree from 'ors-vue-json-tree'
import VueScrollTo from 'vue-scrollto'
import RequestBuilder from '@/pages/api-docs/services/request-builder'
import OrsMap from '@/fragments/ors-map/OrsMap.vue'
import OrsMapBuilder from '@/fragments/ors-map/services/map-builder'
import OrsTableBuilder from '@/fragments/ors-table/services/table-builder'
import PrettyCodeViewer from '@/fragments/pretty-code-viewer/PrettyCodeViewer'
import OrsTable from '@/fragments/ors-table/OrsTable'

export default {
  data () {
    return {
      showMap: false,
      showTable: false,
      showImage: false,
      apiDefinitions: null,
      request: null,
      actionData: null,
      showResponse: false,
      responseFailed: null,
      responseStatusCode: null,
      apiResponse: null,
      apiResponseType: 'json',
      elementId: 'call-result',
      isMaximized: false,
      rawApiResponse: null,
      activeTab: '0',
      responseDuration: null,
      showResponseData: false,
      responseRendered: false,
      orsMapGuid: null,
      actionContainerId: null,
      actionIsMaximized: null
    }
  },
  computed: {
    callResult () {
      if (this.apiResponseType === 'image') {
        return this.request.url
      }
      return this.apiResponse
    }
  },
  created () {
    // Start listening the callAction event.
    // this is the only way to run the caller (emitting the event)
    this.eventBus.$on('runAction', (data) => {
      this.apiDefinitions = data.apiDefinitions
      this.actionData = data.actionData
      this.actionContainerId = data.actionContainerId
      this.actionIsMaximized = data.actionIsMaximized
      this.callAction()
    })

    // when the action is reset, we stop displaying the old response
    this.eventBus.$on('actionReset', () => {
      this.showResponse = false
      this.apiResponse = null
    })
  },
  methods: {
    orsMapCreate (orsMapGuid) {
      this.orsMapGuid = orsMapGuid
    },
    responseDataTabChanged (tabIndex) {
      // '0' equals json response tab
      // '1' equals map response tab
      // '2' equals table response tab
      // '3' equals image response tab
      if (tabIndex === '0' && this.responseRendered === false) {
        // we want the json renderer to be reconstructed at this point
        this.showInfo(this.$t('actionRunner.renderingResponse'))
        // We wrap the show json in a timeout to make sure that the `rendering`
        // is displayed before finishing the json rendering
        // If we don't do that, the browser can get freezed trying to do both tasks
        // sequentially and just show the message once the json render component is ready
        // what is not useful
        setTimeout(() => {
          this.showResponseData = true
        }, 500)
      }
      if (tabIndex === '1') {
        this.eventBus.$emit('redrawAndFitMap', {isMaximized: this.isMaximized, guid: this.orsMapGuid})
      }
    },
    renderingFinished () {
      this.responseRendered = true
      // We only need to show the rendering finished toaster
      // if the response was 200, because in this case the json data
      // can be so big that it may take a while to be rendered
      // If the response status code is not OK (200) then the json
      // data t be rendered is too small and no rendering/rendered
      // toaster is needed
      if (this.responseStatusCode === 200) {
        this.showSuccess(this.$t('actionRunner.renderingFinished'), {timeout: 2000})
      }
    },

    /**
     * Check if the ors map component has support for the current request endpoint
     *
     * @returns {Boolean}
     */
    resourceSupportsMap () {
      let data = {
        responseData: this.apiResponse,
        requestData: this.request,
        apiVersion: this.$store.getters.apiVersion
      }
      return OrsMapBuilder.hasMapBuilderFor(data)
    },

    /**
     * Check if the ors table component has support for the current request endpoint
     *
     * @returns {Boolean}
     */
    resourceSupportsTable () {
      let data = {
        responseData: this.apiResponse,
        endpoint: this.request.endpoint,
        apiVersion: this.$store.getters.apiVersion
      }
      return OrsTableBuilder.hasTableBuilderFor(data)
    },
    resourceSupportsCode () {
      return this.apiResponseType !== 'image'
    },
    resourceSupportsImage () {
      return this.apiResponseType === 'image'
    },
    /**
     * When the container box is resized
     *
     * @param {*} data
     */
    boxResized (data) {
      this.isMaximized = data.maximized
      // Redraw the map to the new box size
      this.eventBus.$emit('redrawAndFitMap', {isMaximized: this.isMaximized, guid: this.orsMapGuid})
    },

    /**
     * Get and request object and validate it
     * If it has any property that represents an invalid state
     * shows and error toast message and return false
     *
     * @returns {Object}|false
     */
    getAndValidateRequest () {
      let requestBuilder = new RequestBuilder(this.actionData, this.apiDefinitions)
      let requestData = requestBuilder.getRequest()
      if (requestData.missingApiKey === true) {
        this.showError(this.$t('actionRunner.anAPIKeyMustBeInputted'))
        return false
      } else if (requestData.missingRequiredParam === true) {
        this.showError(this.$t('actionRunner.missingRequiredParam'))
        return false
      } else {
        requestData.data = requestData.body
      }
      return requestData
    },

    /**
     * Call the API action, display the result and
     * scroll the page to the result container
     *
     */
    callAction () {
      this.request = this.getAndValidateRequest()

      // Reset response/data rendering state
      this.responseDuration = null
      this.rawApiResponse = null
      this.showMap = this.showTable = this.showImage = false
      this.responseRendered = false
      this.showResponseData = false // we want the json renderer to be destroyed at this point
      this.apiResponseType = 'json'

      if (this.request) {
        // Show the info toast while the request is being processed
        // we use a very long time out because as soon as the request is finished
        // the success or error toaster will replace it, so we try to make sure
        // that the info toaster will be displayed until the response come
        this.showInfo(this.$t('actionRunner.runningApiRequest'), {timeout: 20000})
        // The only possible http methods for the ORS api are: GET or POST
        let requestPromise
        if (this.request.method === 'get') {
          requestPromise = HttpOrsApi.get(this.request.url, this.request.headers)
        } else { // it is a POST
          requestPromise = HttpOrsApi.post(this.request.url, this.request.body, this.request.headers)
        }

        let context = this
        requestPromise.then(response => {
          this.handleSuccessResponse(response)
        })
        .catch(response => {
          this.handleErrorResponse(response)
        }).finally(function () {
          context.$emit('apiCallEnd')
          context.eventBus.$emit('apiCallEnd')
          context.scrollToResult()
        })
      } else {
        this.$emit('apiCallEnd')
        this.eventBus.$emit('apiCallEnd')
      }
    },

    /**
     * Handle the API response error, checking the status code and displaying the error toaster.
     * If no response status is present we guess it is an authorization error
     *
     * @param {*} response
     */
    handleErrorResponse (response) {
      this.showResponse = true
      this.responseFailed = true
      this.activeTab = '0'
      this.responseDataTabChanged(this.activeTab)
      let responseType = response.headers['content-type'].toLowerCase()
      this.apiResponseType = responseType.startsWith('application/json') ? 'json'
          : 'text'

      // In case of error the json data returned is not big
      // so we can directly show this. It will not freeze the browser
      this.showResponseData = true

      // If no response status is present we guess it is an authorization error.
      // As we may be running a Cross Site Script Request (when the target domain is different)
      // and the request is ran direct in the browser, using javascript, the pre-flight request
      // will fail because the API is not returning a valid response in case the API key is invalid or absent
      // In this case our request will fail. But as it was agreed with the team that this is the only case the
      // API returns an invalid response, we are considering this as an invalidApiKey case.
      if (!response.status) {
        this.responseStatusCode = 403
        this.apiResponse = this.$t('actionRunner.invalidApiKey')
      } else { // all the other cases comes here
        if (response.message || response.data) {
          this.responseStatusCode = response.status
          this.apiResponse = response.data || response.message
        } else {
          this.showResponse = false
          console.log(response)
        }
      }
      this.showError(this.$t('actionRunner.apiResponseFailed'))
      this.$forceUpdate()
    },
    /**
     * Handle the API success case, checking the data,
     * the status and formatting the response to be displayed
     *
     * @param {*} response
     */
    handleSuccessResponse (response) {
      if (response && response.data) {
        // The response content type be only in the `json` or `gpx`. The geojson is also considered as `json` for this case
        let responseType = response.headers['content-type'].toLowerCase()
        this.apiResponseType = responseType.startsWith('image') ? 'image'
          : responseType.startsWith('application/json') ? 'json'
            : 'xml'
        if (this.request.endpoint === '/matrix') {
          this.activeTab = '1' // table tab active by default for matrix
        } else {
          this.activeTab = this.resourceSupportsMap() || this.resourceSupportsImage() ? '1' : '0' // map/image or json code tab active
        }
        this.responseDataTabChanged(this.activeTab)
        this.showMap = this.showTable = this.showImage = true
        this.responseStatusCode = response.status
        this.responseFailed = response.status.toString().startsWith('4') || response.status.toString().startsWith('5')
        this.apiResponse = this.rawApiResponse = response.data
        this.showResponse = true
        this.responseDuration = response.duration
        this.showSuccess(this.$t('actionRunner.apiResponseReady'), {timeout: 2000})
      } else {
        this.activeTab = '0'
        this.responseDataTabChanged(this.activeTab)
        this.apiResponse = ''
        this.showResponse = false
        console.log(response)
        this.showError(this.$t('actionRunner.apiResponseFailed'))
      }
    },

    /**
     * Scroll to the result
     *
     */
    scrollToResult () {
      if (!this.isMaximized) {
        let element = this.elementId
        let options = { easing: 'ease-in', offset: -100, x: false, y: true }

        // if the
        if (this.actionIsMaximized) {
          options.container = `#${this.actionContainerId}`
        } else {
          options.container = 'body'
        }

        setTimeout(() => {
          VueScrollTo.scrollTo(`#${element}`, 2000, options)
        }, 1000)
      }
    },
    /**
     * Force the download of the response string body
     */
    download () {
      // The way to force a native browser download of a string is by
      // creating a hidden anchor and setting its href as a data text
      let link = document.createElement('a')

      // Create file name parts
      let extension = this.apiResponseType === 'image' ? 'png' : this.apiResponseType === 'json' ? 'json' : 'gpx'
      let endpoint = this.actionData.endpoint.replace(/\//g, '_')
      let verb = this.actionData.verb
      let timestamp = new Date().getTime()

      // Set the download content from the response body/content for gpx and json
      // Set png content from url
      let content
      if (extension === 'png') {
        link.href = this.request.url
        link.target = '_blank'
      } else if (extension === 'json') {
        content = JSON.stringify(this.rawApiResponse)
        link.href = 'data:text/plain;charset=utf-8,' + encodeURIComponent(content)
      } else {
        content = this.rawApiResponse.toString()
        link.href = 'data:text/plain;charset=utf-8,' + encodeURIComponent(content)
      }

      // Set the filename
      link.download = `ors_${endpoint}_${verb}_${timestamp}.${extension}`

      // Fire the download by triggering a click on the hidden anchor
      document.body.appendChild(link)
      // Treat png download
      if (extension === 'png') {
        let dialogOptions = {
          title: this.$t('actionRunner.attributionConfirmationTitle'),
          text: this.$t('actionRunner.attributionConfirmationText'),
          options: {markdown: true, yes: this.$t('actionRunner.attributionConfirmationYes')}
        }
        this.confirmDialog(dialogOptions.title, dialogOptions.text, dialogOptions.options).then(() => {
          link.click()
        })
      } else {
        link.click()
        link.remove()
      }
    }
  },
  components: {
    VueMarkdown,
    JsonTree,
    OrsMap,
    PrettyCodeViewer,
    OrsTable
  }
}



