export default {
  props: {
    resources: {
      required: true
    },
    apiDefinitions: {
      required: true
    },
    apiVersions: {
      required: true,
      type: Array
    }
  },
  data () {
    return {
      topPosition: null,
      menuOpen: false,
      apiVersion: null
    }
  },
  // we want to listen to the scroll event
  created () {
    window.addEventListener('scroll', this.handleScroll)
    this.apiVersion = this.$store.getters.apiVersion || this.apiVersions[0]
  },
  watch: {
    /**
     * Reload the API doc file every time the selected API version changes
     */
    'apiVersion' () {
      this.$store.commit('apiVersion', this.apiVersion)
      this.$emit('apiVersionChanged', this.apiVersion)
    }
  },
  methods: {
    navigateTo (item) {
      this.menuOpen = false
      this.$emit('navigateTo', item)
    },
    // Change the position of the navigation element on the screen according the scroll, so it is always visible
    // we decided not to use the simples css 'fixed' because it breaks other render
    // aspects as splitting the windows width using vuetify flex
    handleScroll () {
      // the reaction of repositioning the menu is wrapped in a timeout to
      // make the element moving more smoothly
      setTimeout(() => {
        // the menu should move down only if the page has scrolled more then 150px
        let scroll = document.documentElement.scrollTop || document.body.scrollTop
        let offset = this.$vuetify.breakpoint.mdAndDown ? 50 : 120
        if (scroll > 150) {
          this.topPosition = (scroll - offset) + 'px'
        } else {
          this.topPosition = null
        }
      }, 1000)
    }
  },
  computed: {
    isAbsolute () {
      return this.topPosition === null && this.$vuetify.breakpoint.mdAndDown
    },
    menuItems () {
      // Create the fixed section of the menu
      const menuItems = [
        { 'header': this.$t('apiNavigation.presentation') },
        { 'anchor': '/introduction', 'title': this.$t('apiNavigation.introduction'), 'icon': 'home' }
      ]

      // If the service info has the security attribute, add the security menu item
      if (this.apiDefinitions && this.apiDefinitions.security) {
        menuItems.push({ 'anchor': '/authorization', 'title': this.$t('apiNavigation.authorization'), 'icon': 'lock', 'group': 'pages' })
      }

      // After the predictable items, add the divider and the header for the dynamic elements
      menuItems.push({ divider: true })
      menuItems.push({ 'header': this.$t('apiNavigation.reference') })

      // Build the dynamic elements
      if (this.resources) {
        for (var resourceGroupName in this.resources) {
          let item = { 'anchor': '/' + this.resources[resourceGroupName].path, 'title': resourceGroupName, 'icon': 'map', items: [] }

          for (var actionIndex in this.resources[resourceGroupName].actions) {
            let action = this.resources[resourceGroupName].actions[actionIndex]
            item.items.push({ 'anchor': action.path, 'title': `${action.endpoint} [${action.verb.toUpperCase()}]`, 'icon': 'http' })
          }

          menuItems.push(item)
        }
      }

      // At the bottom add the separator and the header that will work as header to the api version selector
      menuItems.push({ divider: true })
      menuItems.push({ 'header': this.$t('apiNavigation.selectApiVersion') })

      return menuItems
    }
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}



