import VueMarkdown from 'vue-markdown'
import PrettyCodeViewer from '@/fragments/pretty-code-viewer/PrettyCodeViewer'
import RequestBuilder from '@/pages/api-docs/services/request-builder'
import ActionRunner from './components/action-runner/ActionRunner'

// From here we have all the GET code template markdown as components
// that at the end only contain markdown texts
import JavascriptGetTemplate from './code-example-templates/get/javascript.md'
import cURLGetTemplate from './code-example-templates/get/curl.md'
import JavaGetTemplate from './code-example-templates/get/java.md'
import NodeJSGetTemplate from './code-example-templates/get/node-js.md'
import PerlGetTemplate from './code-example-templates/get/perl.md'
import PythonGetTemplate from './code-example-templates/get/python.md'
import PHPGetTemplate from './code-example-templates/get/php.md'
import RubyGetTemplate from './code-example-templates/get/ruby.md'
import GoGetTemplate from './code-example-templates/get/go.md'
import CSharpGetTemplate from './code-example-templates/get/c-sharp.md'
import VisualBasicGetTemplate from './code-example-templates/get/visual-basic.md'
import GroovyGetTemplate from './code-example-templates/get/groovy.md'
import ObjectiveCGetTemplate from './code-example-templates/get/objective-c.md'
import SwiftGetTemplate from './code-example-templates/get/swift.md'
// Finished the markdown code template components import

// From here we have all the POST code template markdown as components
// that at the end only contain markdown texts
import JavascriptPostTemplate from './code-example-templates/post/javascript.md'
import cURLPostTemplate from './code-example-templates/post/curl.md'
import JavaPostTemplate from './code-example-templates/post/java.md'
import NodeJSPostTemplate from './code-example-templates/post/node-js.md'
import PerlPostTemplate from './code-example-templates/post/perl.md'
import PythonPostTemplate from './code-example-templates/post/python.md'
import PHPPostTemplate from './code-example-templates/post/php.md'
import RubyPostTemplate from './code-example-templates/post/ruby.md'
import GoPostTemplate from './code-example-templates/post/go.md'
import CSharpPostTemplate from './code-example-templates/post/c-sharp.md'
import VisualBasicPostTemplate from './code-example-templates/post/visual-basic.md'
import GroovyPostTemplate from './code-example-templates/post/groovy.md'
import ObjectiveCPostTemplate from './code-example-templates/post/objective-c.md'
import SwiftPostTemplate from './code-example-templates/post/swift.md'
// Finished the markdown code template components import

export default {
  props: {
    actionData: {
      required: true
    },
    apiDefinitions: {
      required: true
    },
    actionContainerId: {
      required: false
    },
    actionIsMaximized: {
      default: false
    }
  },
  data () {
    return {
      // Array containing key-value-pairs of languages available to template
      codeExampleLanguages: [
        {id: 'Javascript', text: 'Javascript'},
        {id: 'cURL', text: 'cURL'},
        {id: 'Java', text: 'Java'},
        {id: 'NodeJS', text: 'Node.js'},
        {id: 'Perl', text: 'Perl'},
        {id: 'Python', text: 'Python'},
        {id: 'PHP', text: 'PHP'},
        {id: 'Ruby', text: 'Ruby'},
        {id: 'CSharp', text: 'C#'},
        {id: 'Go', text: 'Go'},
        {id: 'VisualBasic', text: 'Visual Basic'},
        {id: 'Groovy', text: 'Groovy'},
        {id: 'ObjectiveC', text: 'Objective-C'},
        {id: 'Swift', text: 'Swift'}
      ],
      callingAction: false,
      codeExampleSource: null,
      showCodeExample: false,
      showResponse: false
    }
  },
  created () {
    // on the creation, set the first language as the selected language
    this.selectedCodeLanguage = this.codeExampleLanguages[0]

    // build the source on the creation, using the selected language
    this.buildSource()

    // start listening the parameters change. If they change, rebuild the source
    this.eventBus.$on('fieldUpdated', () => {
      this.buildSource()
    })

    this.eventBus.$on('apiCallEnd', () => {
      this.apiCallEnded()
    })
  },
  watch: {
    'actionData' () {
      this.eventBus.$emit('actionReset')
      this.buildSource()
    }
  },
  computed: {
    /**
     * Return a list of code template language key
     *
     * @returns
     */
    codeTemplates () {
      return {
        JavascriptGetTemplate,
        cURLGetTemplate,
        JavaGetTemplate,
        NodeJSGetTemplate,
        PerlGetTemplate,
        PythonGetTemplate,
        PHPGetTemplate,
        RubyGetTemplate,
        GoGetTemplate,
        CSharpGetTemplate,
        VisualBasicGetTemplate,
        GroovyGetTemplate,
        ObjectiveCGetTemplate,
        SwiftGetTemplate,
        JavascriptPostTemplate,
        cURLPostTemplate,
        JavaPostTemplate,
        NodeJSPostTemplate,
        PerlPostTemplate,
        PythonPostTemplate,
        PHPPostTemplate,
        RubyPostTemplate,
        GoPostTemplate,
        CSharpPostTemplate,
        VisualBasicPostTemplate,
        GroovyPostTemplate,
        ObjectiveCPostTemplate,
        SwiftPostTemplate
      }
    },
    /**
     * The call resource btn text depend on the call resource status. While pending it receives a different text
     *
     * @returns string
     */
    callActionText () {
      return this.callingAction ? this.$t('apiExamplesActionFooter.calling') : this.$t('apiExamplesActionFooter.callAction')
    },
    /**
     * The show code example btn btn text depend on the example block display status
     *
     * @returns
     */
    showCodeText () {
      return this.showCodeExample ? this.$t('apiExamplesActionFooter.hideExampleCode') : this.$t('apiExamplesActionFooter.showExampleCode')
    }
  },
  methods: {
    gpxRenderingFinished () {
      this.showSuccess(this.$t('apiExamplesActionFooter.gpxRenderingFinished'), {timeout: 2000})
    },
    gpxRenderingStarted () {
      this.showSuccess(this.$t('apiExamplesActionFooter.gpxRenderingStarted'), {timeout: 2000})
    },
    /**
     *Change the language template example selected
     *
     * @param String language
     */
    codeLanguageChanged (language) {
      this.selectedCodeLanguage = language
      this.buildSource()
    },
    /**
     *Change the calling stat to not pending
     *
     */
    apiCallEnded () {
      this.callingAction = false
    },
    /**
     * Show/hide code example component
     */
    toggleShowCodeExample () {
      this.showCodeExample = !this.showCodeExample
      if (this.showCodeExample) {
        this.showResponse = false
      }
    },
    /**
     * Prepare the properties to call the resource, hiding the example code and
     * emits the `callAction` event, that is listened by the ActionRunner,
     * passing the necessary data
     */
    callAction () {
      this.showCodeExample = false
      this.callingAction = true
      this.eventBus.$emit('validateAndSubmitAPIRequest')
    },
    /**
     * Build the source template to be used to demonStrate how to
     * use a certain language to make a request to ORS API
     */
    buildSource () {
      // the template unique key has following pattern: `<CapitalizedLanguageName>CapitalizedVerbTemplate`, like 'JavascriptGetTemplate'
      let templateId = this.selectedCodeLanguage.id
      let capitalizedVerb = this.$options.filters.capitalize(this.actionData.verb)
      let codeTemplateId = `${templateId}${capitalizedVerb}Template`
      let template = this.codeTemplates[codeTemplateId]
      if (template) {
        let requestBuilder = new RequestBuilder(this.actionData, this.apiDefinitions)
        let request = requestBuilder.getRequest()

        // Build the codeExampleSource based on the selected template and replace the placeholder
        // by real values. Some template may not contains
        // all the place holders, but this is not a problem
        let parsedTemplate = template
        .replace('<url>', request.url)
        .replace('<baseUrl>', request.baseUrl)
        .replace('<path>', request.path)

        if (request.body) {
          parsedTemplate = parsedTemplate.replace('<body>', JSON.stringify(request.body))
        }

        this.codeExampleSource = parsedTemplate
      } else {
        this.codeExampleSource = ''
      }
      // this.$forceUpdate()
    }
  },
  components: {
    VueMarkdown,
    ActionRunner,
    PrettyCodeViewer
  }
}



