/**
 * OUTPUT EXAMPLE
 * This adapter (api version 1), through the exposed method parseApiDocData outputs an object with following structure:
  {
    apiDefinitions: {
      baseUrl: String,
      host: String,
      scheme: String,
      produces: String,
      description: String,
      version: String,
      title: String,
      security: { // this attribute can be present or not, according the doc source data
        strategy: String,
        type: String,
        paramName: String,
        description: String,
        in: String (query|header|body),
        name: String
    },
    resources: [
      `ResourceName`: {
        name: String,
        key: String (unique)
        path: String,
        description: String,
        actions: [
          {
            verb: String (get|put|post|delete|patch),
            path: String,
            key: String (unique),
            endpoint: String,
            description: String,
            parameters: [
              {
                name: String,
                required: boolean,
                type: String,
                in: String (query|body|header),
                description: String,
                default: String|Array (nullable),
                isEnum: boolean,
                enum: [Value1, Value2,...],
                value: String|boolean,
                label: String,
                inputType: String
                inputTypeStep: Integer|null
                separator: String|null
                example: String|Object
                apiDefault: Object|String,
                props (optional): {
                  [index]: {
                    name: String (unique)
                    type: String
                    description: String,
                    label: String
                  }
                }
              },
              ...
            ],
            responses: [
              `http-status-code-number`: {
                description: String,
                example: Object
              },
              ...
            ],
            summary: String,
            tags: [
              `ResourceName`
            ]
          },
          ...
        ]
      },
      ...
    ]
  }
**/
import utils from '@/support/utils'
import store from '@/store/store'

/**
 * Custom exception for API doc
 * @param {*} id
 * @param {*} message
 */
const ApiDocParseException = (message) => {
  this.message = message
  this.name = 'ApiDocException'
}

/**
 * Parse the raw swagger file into a valid json object describing the api
 * @param {*} rawData
 * @returns {}
 */
const parseApiDocData = (jsonData) => {
  try {
    if (jsonData) {
      let apiDocJson = {}
      apiDocJson.apiDefinitions = parseServiceDefinitions(jsonData)
      apiDocJson.resources = parseResources(jsonData, apiDocJson.apiDefinitions)
      return apiDocJson
    }
  } catch (error) {
    throw new ApiDocParseException('parse_doc_error')
  }
}

/**
 * Parse the api doc resource groups and its paths
 * @param {*} jsonData
 * @return {} resourceGroups
 */
const parseResources = (jsonData, apiDefinitions) => {
  let resourceGroups = {}

  if (jsonData) {
    for (var tagIndex in jsonData.tags) {
      let tag = jsonData.tags[tagIndex]
      resourceGroups[tag.name] = {
        name: tag.name,
        key: '_' + utils.slug(tag.name), // create a unique key starting with '_'. We trust the there will not be two resources with the same name
        path: tag.name.toLowerCase(),
        description: tag.description,
        actions: buildActions(jsonData, apiDefinitions, tag.name)
      }
    }
  }
  return resourceGroups
}

/**
 * Build resource actions based on the resource paths
 * @param {*} jsonData
 * @param {*} apiDefinitions
 * @param {*} resourceName
 */
const buildActions = (jsonData, apiDefinitions, resourceName) => {
  let actions = []
  var userTokens = store.getters.userTokens ? store.getters.userTokens : false
  for (var prop in jsonData.paths) {
    // skip loop if the property is from prototype
    if (!jsonData.paths.hasOwnProperty(prop)) continue

    for (var verb in jsonData.paths[prop]) {
      let action = jsonData.paths[prop][verb]
      if (action.tags.indexOf(resourceName) > -1) {
        action.verb = verb
        action.path = `${prop}/${verb}`
        action.key = `${utils.getValidId(prop)}_${verb}`
        action.endpoint = prop

        // if there is user tokens and the action has parameters
        // run the security field [api_key] formatter
        if (userTokens && action.parameters) {
          formatSecurityField(action, apiDefinitions, userTokens)
        }
        buildActionParameters(action)
        actions.push(action)
      }
    }
  }
  return actions
}

/**
 * The action parameters are formatted and transformed from
 * Array of objects to object with sub objects because it is easier to
 * keep the view synchronized using object then array
 * @param {} action
 * @see https://vuejs.org/v2/guide/list.html#Array-Change-Detection
 */
const buildActionParameters = (action) => {
  // here the parameters object is initialized
  // it is gonna store all the parameters
  let objectParameters = {}

  if (action.parameters) {
    for (var key in action.parameters) {
      // copy the current parameter object from action property to
      // our custom parameter object
      // at the end it will be added as a sub property to
      // the `parameters`
      let parameter = action.parameters[key]

      // format/adjust parameter attributes
      formatParameter(parameter)

      // add the parameter as an attribute of the root parameter object
      objectParameters[key] = parameter
    }
    // Overwrite the action array parameters attribute with our object parameters
    action.parameters = objectParameters
  } else {
    action.parameters = []
  }
}

/**
 * Format an action parameter
 * @param {*} parameter
 */
const formatParameter = (parameter) => {
  // the parameter label is defined according the type and where it is expected to be used (query, body or header)
  if (parameter.in === 'body') {
    parameter.label = '(json) '
  } else { // there is no type defined and it is not a body parameter
    parameter.label = '(' + parameter.type + ') '
  }

  // parse the values to string because they are expected to be strings
  if (parameter.apiDefault !== undefined && parameter.apiDefault !== null) parameter.apiDefault = parameter.apiDefault.toString()
  if (parameter.example !== undefined && parameter.example !== null) parameter.example = parameter.example.toString()

  parameter.inputType = parameter.type === 'integer' || parameter.type === 'number' ? 'number' : 'text'
  parameter.inputTypeStep = parameter.type === 'integer' ? 1 : null

  // Define the separator to be used to collections
  parameter.separator = parameter.collectionFormat === 'pipes' ? '|' : ','

  // define the field example text if is of type body
  if (parameter.in === 'body' && parameter.schema && parameter.schema.example) {
    parameter.example = parameter.schema.example
  }

  // define if the field is a enum or not (select or text input)
  parameter.isEnum = parameter.enum && parameter.enum.length > 0

  // Format the field boolean enum
  if (parameter.type === 'boolean') {
    parameter.isEnum = true
    parameter.enum = ['true', 'false'] // make sure it is not 0 and 1
  }

  setParameterProps(parameter)
}

/**
 * Set the parameter props as object and adjust values
 * @param {*} parameter
 */
const setParameterProps = (parameter) => {
  if (parameter.props && Array.isArray(parameter.props)) {
    let propsAsObj = {}

    // Set the prop type and input step, if integer
    for (let index = 0; index < parameter.props.length; index++) {
      let prop = parameter.props[index]
      if (prop.type) {
        prop.inputType = prop.type === 'integer' || prop.type === 'number' ? 'number' : 'text'
        prop.inputTypeStep = prop.type === 'integer' ? 1 : null
      } else {
        prop.type = 'string'
      }
      prop.label = '(' + prop.type + ') '
      prop.in = parameter.in

      // If the prop has not a description (used in the help modal)
      // we clone the parent description
      if (!prop.description) {
        prop.description = parameter.description
      }
      // it can be recursive. The prop can have sub props and so on
      setParameterProps(prop)

      // Assign the value
      propsAsObj[index] = prop
    }
    parameter.props = propsAsObj
  }
}

/**
 * Format security field [api key] depending if a user is authenticated or not
 * @param {*} action
 * @param {*} apiServiceInfo
 * @param {*} userTokens
 */
const formatSecurityField = (action, apiDefinitions, userTokens) => {
  for (var key in action.parameters) {
    if (apiDefinitions.security && apiDefinitions.security.paramName === action.parameters[key].name) {
      action.parameters[key].enum = userTokens
      action.parameters[key].defaultIsValid = true
      action.parameters[key].value = action.parameters[key].default = action.parameters[key].enum[0]
      break
    }
  }
}

/**
 * Parse the api doc service info
 * @param {*} jsonData
 * @return {} apiDefinitions
 */
const parseServiceDefinitions = (jsonData) => {
  if (jsonData) {
    let apiDefinitions = {
      baseUrl: `${jsonData.schemes[0]}://${jsonData.host}`,
      host: jsonData.host,
      scheme: `${jsonData.schemes[0]}://`,
      produces: jsonData.produces[0],
      description: jsonData.info.description,
      version: jsonData.info.version,
      title: jsonData.info.title
    }

    if (jsonData.definitions) {
      apiDefinitions.definitions = jsonData.definitions
    }

    // if the json document has the security section, transform/add the security data to the apiDefinitions
    if (jsonData.security && Array.isArray(jsonData.security) && jsonData.security.length > 0) {
      // get all the object attributes as an array of strings, each one representing the object attribute name
      let securityAttributes = Object.keys(jsonData.securityDefinitions)

      // We are considering only the first security strategy.
      let firstApiSecurity = jsonData.securityDefinitions[securityAttributes[0]]

      // initialize the security object
      apiDefinitions.security = {}

      // According the api doc specification, it is expected to have the security type as first attribute of the object
      apiDefinitions.security.strategy = securityAttributes[0]

      apiDefinitions.security.type = firstApiSecurity.type

      // According the api doc specification, it is expected to have the security param name at the first position
      // of array defined as value of the first security the object example: security.UserSecurity['api_key']
      apiDefinitions.security.paramName = firstApiSecurity.name

      // The security description is stored in the securityDefinitions object under an attribute with the same name of the first security object name
      // example: securityDefinitions.UserSecurity.description
      apiDefinitions.security.description = firstApiSecurity.description

      apiDefinitions.security.in = firstApiSecurity.in
    }

    return apiDefinitions
  }
}

/**
 * Tries to find the matching resource and action according a given path
 * @param {*} path
 * @param {*} resources
 */
const getActionAndResourceByPath = (path, resources) => {
  let pathParts = path.split('/')

  // The url only corresponds to an action + http action if it has more then one segment
  // example: `directions/get`, `geocode/search/structured/get`, `matrix/post`
  if (pathParts.length > 1) {
    // The action http verb is always the last segment
    let verb = pathParts[pathParts.length - 1]

    // the action path is always all the path minus the http verb
    // we also remove the initial and ending slash to make sure
    // that we are always working the the expected patter
    let actionPath = '/' + path.replace(verb, '').replace(/^\/|\/$/g, '')

    let found = null

    for (var resourceKey in resources) {
      let resource = resources[resourceKey]

      // we have to go through all resource actions to check
      // if it contains one which endpoint matches with the actionPath
      for (var actionKey in resource.actions) {
        let action = resource.actions[actionKey]
        if (action.endpoint === actionPath && action.verb === verb) {
          found = { action, resource }
          break
        }
      }

      if (found) {
        break
      }
    }
    return found
  }
}

/**
 * Return adapter object with the exposed methods
 */
const apiDocAdapterV1 = {
  getActionAndResourceByPath,
  parseApiDocData
}

export default apiDocAdapterV1



