import FieldsContainer from './components/fields-container/FieldsContainer'
import ApiResponses from './components/responses/Responses'
import VueScrollTo from 'vue-scrollto'
import Form from '@/core/form'

export default {
  props: {
    actionData: {
      required: true
    },
    apiDefinitions: {
      required: true
    },
    actionContainerId: {
      required: false
    },
    actionIsMaximized: {
      default: false
    }
  },
  created () {
    let context = this
    // Start listening the validateAndSubmitAPIRequest event.
    this.eventBus.$on('validateAndSubmitAPIRequest', () => {
      this.submit(null, context)
    })
  },
  data () {
    return {
      activeTab: '0', // the default tab is always the first (uri parameters)
      totalTabs: 5 // total including the not rendered ones for certain cases
    }
  },
  computed: {
    hasPathParameter () {
      let pathParameters = this.lodash.filter(this.actionData.parameters, function (p) { return p.in === 'path' })
      return pathParameters.length > 0
    }
  },
  watch: {
    /**
     * Watches for the `actionData` update that occurs when this prop in changed in the parent component
     * so it indicates the the action object has changed (user has selected another action)
     */
    'actionData' () {
      this.activeTab = '0' // the default tab is always the first (uri parameters)
    }
  },
  methods: {
    /**
     * Propagate the change to the parent
     * @param {*} data
     */
    fieldUpdated (data) {
      // When a select option field is changed, the value of the action dta object
      // is not automatically updated. so we have to update it here
      // `data.value` will only exist is an select option field was changed
      if (data.value !== undefined) {
        this.actionData.parameters[data.index].value = data.value
      }
      this.$emit('fieldUpdated', data)
    },
    submit (event, context) {
      let vm = context || this

      if (vm.$refs.APIForm) {
        let form = new Form(vm.$refs.APIForm, this)

        if (form.validate()) {
          let data = {
            apiDefinitions: this.apiDefinitions,
            actionData: this.actionData,
            actionContainerId: this.actionContainerId,
            actionIsMaximized: this.actionIsMaximized
          }
          vm.eventBus.$emit('runAction', data)
        } else {
          this.goToFirstInvalidInput(vm.$refs.APIForm)

          // Tell the footer component that the cal has been ended/aborted
          // this is necessary then the action was run by the hitting the
          // footer component button
          vm.eventBus.$emit('apiCallEnd')

          // Show toaster with error message
          vm.showError(this.$t('apiExamplesConsole.invalidFields'))
        }
      }
    },

    getParentTabItem (component) {
      if (component.$parent.constructor.options.name === 'v-tab-item') {
        return component.$parent
      } else {
        return this.getParentTabItem(component.$parent)
      }
    },
    goToFirstInvalidInput (formRef) {
      let firstInvalidInput = this.lodash.find(formRef.getInputs(), function (i) { return !i.valid })

      if (firstInvalidInput) {
        let firstInvalidTab = this.getParentTabItem(firstInvalidInput)

        // We can have not rendered tabs for certain endpoints
        // so the tab key may change according the amount rendered
        // We get the real tab key based on its index looping over all tabs and
        // comparing the declared key with the target one
        let targetIndex = this.lodash.findIndex(this.$refs.tabsRef.tabs, function (t) { return t.$vnode.key === firstInvalidTab.$vnode.key })

        // Active tab must a string
        this.activeTab = targetIndex.toString()

        // Wait the tab switch and scroll to the element
        setTimeout(() => {
          let options = {container: 'body', easing: 'ease-in', offset: -100, x: false, y: true}
          VueScrollTo.scrollTo(firstInvalidInput.$el, 2000, options)
        }, 500)
      }
    }
  },
  components: {
    FieldsContainer,
    ApiResponses
  }
}



