import ModelService from '@/core/model-service'
import apiDocAdapterV1 from './adapters/api-doc-adapter-v1'
import store from '@/store/store'

let options = {
  pk: 'version',
  raw: true, // we dont need each menu resource to be converted to a Model (@/core/model), because it is a read-only resource
  transformResponse: (response) => {
    if (response.config.method === 'get') {
      response.data = getAdapter(response.pkValue).parseApiDocData(response.data)
    }
  }
}

const getAdapter = () => {
  // This is a dictionary that translates
  // the API version to an adapter version
  let apiVersionToAdapterVersionDictionary = {
    '4.5': 'V1',
    '4.7': 'V1'
    // we can add more versions here in the future
  }

  // const with the list of adapters version
  let adapterVersions = {
    apiDocAdapterV1
    // we can add more versions here in the future
  }
  // get the current API version stored in our vuex store
  let apiVersion = store.getters.apiVersion

  // Get the adapter based on the api version
  let adapterVersionName = 'apiDocAdapter' + apiVersionToAdapterVersionDictionary[apiVersion]

  return adapterVersions[adapterVersionName]
}

const apiDocService = new ModelService('ors-api/v1/api-doc/source', 'API Documentation', options)

apiDocService.getAdapter = getAdapter

export default apiDocService



