/**
 * Request Builder class that build a request based on the
 * action data and api definitions provided
 * @param {*} actionData
 * @param {*} apiDefinitions
 */
function RequestBuilder (actionData, apiDefinitions) {
  this.actionData = actionData
  this.apiDefinitions = apiDefinitions
  /**
   * Get the the request data containing all data need to run the request
   * @param buildOnlyUrl
   * @returns {*} requestData
   */
  this.getRequest = (buildOnlyUrl = false) => {
    let requestData = {
      method: this.actionData.verb,
      baseUrl: this.apiDefinitions.baseUrl,
      endpoint: this.actionData.endpoint,
      path: '?',
      // GET and DELETE http verbs do not accept body content in the request
      body: this.actionData.verb === 'get' || this.actionData.verb === 'delete' ? null : {},
      headers: {},
      missingApiKey: false,
      missingRequiredParam: false
      // responseType: 'application/json;charset=UTF-8'
    }

    if (actionData && this.actionData.parameters) {
      for (let key in this.actionData.parameters) {
        let param = this.actionData.parameters[key]
        this.addParameter(param, requestData, buildOnlyUrl)
        if (requestData.missingRequiredParam || requestData.missingApiKey) {
          break
        }
      }
    }
    requestData.path = requestData.endpoint + requestData.path
    requestData.url = requestData.baseUrl + requestData.path
    return requestData
  }

  /**
   * Add a parameter to the request data in the correct slot (query, body or header)
   *
   * @param {*} param
   * @param {*} requestData
   * @param {boolean} buildOnlyUrl
   */
  this.addParameter = (param, requestData, buildOnlyUrl = false) => {
    this.validateParam(param, requestData)
    if (param.valid) {
      if (param.in === 'query') {
        if (requestData.path !== '?') {
          requestData.path += '&'
        }
        let value = Array.isArray(param.value) ? param.value.join(param.separator) : param.value
        requestData.path += `${param.name}=${encodeURI(value)}`
      } else if (buildOnlyUrl === false && param.in === 'body' && requestData.body !== null) {
        // valid body json in matrix post: { "profile":"driving-hgv", "locations": [[7.796, 48.35], [11.13, 50.98]]}
        requestData.body = param.value ? JSON.parse(param.value) : null
      } else if (buildOnlyUrl === false && param.in === 'header') {
        requestData.headers[param.name] = param.value
      } else if (param.in === 'path') {
        let placeholder = `{${param.name}}`
        requestData.endpoint = requestData.endpoint.replace(placeholder, param.value)
      }
    }
  }

  /**
   * Define if a param is the special security param defined in the api definitions
   *
   * @param {*} param
   * @returns boolean
   */
  this.isSecurityParam = (param) => {
    let is = false
    if (this.apiDefinitions.security && param.name === this.apiDefinitions.security.paramName) {
      is = true
    }
    return is
  }
  /**
   * Validate the param considering the value and also treat the special one that represents the security key
   * to the API. If any param is invalid, set the `missingApiKey` and/or
   * the `missingRequiredParam` on the requestData object.
   * for each param, we set the `valid` property according the value state
   *
   * @param {*} param
   * @param {*} requestData
   */
  this.validateParam = (param, requestData) => {
    param.valid = true

    // If the parameter has not a valid value, then
    // we define the parameter valid state  as false
    // This status will be checked by the `callResource` method.
    // to define if the parameter must be included in the
    // request or not
    if (param.value === null || param.value === undefined || param.value === '') {
      param.valid = false
    }

    // If the current param is the security parameter (they api key)
    // and it does not contain a valid value, then the define that
    // the request has a missing ey status. This property will be checked
    // by the `callResource` method.
    // The API key has a default value that is not valid, because works as a placeholder
    if (this.isSecurityParam(param) && (param.valid === false || (param.value === param.default && !param.defaultIsValid))) {
      if (requestData) {
        requestData.missingApiKey = true
      }
    }

    // The parameter is required and has not a valid value, then
    // we define the `missingRequiredParam` property that
    // will be checked by the `callResource` method.
    if (param.required && param.valid === false) {
      if (requestData) {
        requestData.missingRequiredParam = true
      }
    }

    // If the parameter value is an array and is empty it should not be used
    // in the request.
    if (Array.isArray(param.value) && param.value.length === 0) {
      param.valid = false
    }
  }
}

// export the request builder class
export default RequestBuilder



