import ModelService from '@/core/model-service'

let options = {
  pk: 'version',
  raw: true // we dont need each menu resource to be converted to a Model (@/core/model), because it is a read-only resource
}

const apiDocVersionService = new ModelService('ors-api/v1/api-doc/versions', 'API Documentation version', options)

export default apiDocVersionService



