import VueMarkdown from 'vue-markdown'
import RequestBuilder from '@/pages/api-docs/services/request-builder'

export default {
  props: {
    actionData: {
      required: true,
      type: Object
    },
    apiDefinitions: {
      required: true,
      type: Object
    }
  },
  data () {
    return {
      fullSize: false,
      urlIsSelected: false,
      endpoint: null
    }
  },
  components: {
    VueMarkdown
  },
  computed: {
    getEndpoint () {
      return this.hasPathParameter() ? this.endpoint : this.actionData.endpoint
    }
  },

  methods: {
    hasPathParameter () {
      let pathParameters = this.lodash.filter(this.actionData.parameters, function (p) { return p.in === 'path' })
      return pathParameters.length > 0
    },
    /**
     * Check whenever a param is valid to be displayed in the url div in the html format
     *
     * @param {*} param
     * @return Boolean
     */
    isValidForUri (param) {
      return param.value && param.in === 'query' && (!Array.isArray(param.value) || param.value.length > 0)
    },
    getParamDisplayValue (param) {
      return Array.isArray(param.value) ? param.value.join(param.separator) : param.value
    },
    /**
     * Get a clean url based on the current action form data
     *
     * @returns
     */
    getCleanUrl () {
      let requestBuilder = new RequestBuilder(this.actionData, this.apiDefinitions)
      // true means `build only url` to avoid using resources to build all the request data
      let request = requestBuilder.getRequest(true)
      return request.url
    },
    /**
     * Add a listener to the url click event so that
     * we can intercept it and auto select the full url and
     * showa toaster explaining this to the user
     * @param {*} event
     */
    urlCopyListener (event) {
      event.clipboardData.setData('text/plain', this.getCleanUrl())
      event.preventDefault()
      this.showSuccess(this.$t('apiExamplesActionPresentation.fullUrlCopied'), {timeout: 2000})
    },

    /**
     * Copy the string to chipboard by creating a temporary textarea element
     *
     * @param {*} str
     * @returns {Boolean}
     */
    copyToClipboard (str) {
      const el = document.createElement('textarea')
      el.value = str
      document.body.appendChild(el)
      el.select()
      let result = document.execCommand('copy')
      document.body.removeChild(el)
      return result
    },

    /**
     * Copy or toggle the url selection state to `on` and show a toaster about this
     */
    copyOrSelectUrl () {
      let context = this
      let selectUrl = () => {
        context.urlIsSelected = true
        context.showInfo(this.$t('apiExamplesActionPresentation.ctrlPlusCToCopy'), {timeout: 2000})
      }
      try {
        let url = this.getCleanUrl()
        if (this.copyToClipboard(url)) {
          this.showSuccess(this.$t('apiExamplesActionPresentation.fullUrlCopied'), {timeout: 2000})
        } else {
          selectUrl()
        }
      } catch (err) {
        selectUrl()
      }
    },

    /**
     * Toggle the url selection state to `off`
     */
    deselectUrl () {
      this.urlIsSelected = false
    },
    updateActionData () {
      let requestBuilder = new RequestBuilder(this.actionData, this.apiDefinitions)

      // `true` means `build only url` to avoid using resources to build all the request data
      let request = requestBuilder.getRequest(true)
      this.endpoint = request.endpoint
      this.$forceUpdate()
    }
  },
  created () {
    // The initial endpoint is the one declared as the action endpoint
    this.endpoint = this.actionData.endpoint
    let context = this
    this.eventBus.$on('fieldUpdated', () => {
      context.updateActionData()
    })
  },
  /**
   * Add a listener to the url div copy event
   * so that we can intercept it and provide the clean url
   * and not the html version that is displayed in the div
   */
  mounted () {
    let urlContainer = this.$el.querySelector('div.action-url')
    urlContainer.addEventListener('copy', this.urlCopyListener)
  },
  /**
   * Remove the url copy listener when this component is destroyed
   */
  destroyed () {
    let urlContainer = this.$el.querySelector('div.action-url')
    urlContainer.removeEventListener('copy', this.urlCopyListener, false)
  }
}



