import ActionConsole from './components/console/Console'
import ActionPresentation from './components/presentation/Presentation'
import ActionFooter from './components/action-footer/ActionFooter'

export default {
  props: {
    actionData: {
      required: false,
      type: Object
    },
    apiDefinitions: {
      required: true,
      type: Object
    },
    open: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      pristine: true,
      isOpen: true,
      isMaximized: false,
      boxGuid: null
    }
  },
  created () {
    this.buildParameters()
    this.isOpen = this.open
  },
  watch: {
    /**
     * Watches for the `actionData` update that occurs when this prop in changed in the parent component
     * so it indicates the the action object has changed (user has selected another action)
     */
    'actionData' () {
      this.pristine = true
      this.buildParameters()
      this.eventBus.$emit('fieldUpdated', this.actionData)
    },
    'open' (newVal) {
      this.isOpen = newVal
    }
  },
  methods: {
    boxCreated (guid) {
      this.boxGuid = guid
    },
    actionClosed () {
      this.actionOpen = false
      this.$emit('actionClosed')
    },
    /**
     * When the container box is resized
     *
     * @param {*} data
     */
    boxResized (data) {
      this.isMaximized = data.maximized
    },
    /**
     * Build the array with the query parameters
     */
    buildParameters () {
      if (this.actionData && this.actionData.parameters) {
        for (var key in this.actionData.parameters) {
          // it is pristine when the data has not been changed by the user
          // so we populate it wit the default value
          if (this.pristine) {
            this.actionData.parameters[key].value = this.actionData.parameters[key].default
          }
        }
      }
    },

    /**
     * Update the pristine status when the user change a parameter value vie interface
     */
    fieldUpdated (data) {
      // after a filed is updated by the user it is not considered pristine anymore
      // this pristine property/state is used to assign or not the default value when the parameters are being built
      this.pristine = false

      this.eventBus.$emit('fieldUpdated', data)

      // VueJS does is not auto refreshing the view
      // when a object sub attribute change, so we tell vue to do it
      this.$forceUpdate()
    }
  },
  components: {
    ActionConsole,
    ActionPresentation,
    ActionFooter
  }
}



