import VueMarkdown from 'vue-markdown'
import FormFields from './components/form-fields/FormFields'

export default {
  props: {
    actionData: {
      required: true
    },
    paramType: {
      required: true, type: String
    },
    apiDefinitions: {
      required: true
    },
    actionContainerId: {
      required: false
    },
    actionIsMaximized: {
      default: false
    }
  },
  created () {
    this.defineBodyExample()
  },
  data () {
    return {
      bodyExample: null,
      tokens: null
    }
  },
  watch: {
    'actionData' () {
      this.defineBodyExample()
    }
  },
  methods: {
    fieldUpdated (data) {
      this.$emit('fieldUpdated', { index: data.index, value: data.value })
    },
    buildLabel (parameter) {
      let label = parameter.label

      if (parameter.example) {
        label += (' ' + this.$t('actionForm.exampleAbbreviation') + ' ' + parameter.example)
      } else if (parameter.apiDefault) {
        label += (' ' + this.$t('actionForm.defaultAbbreviation') + ' ' + parameter.apiDefault)
      }
      return label
    },
    defineBodyExample () {
      if (this.paramType === 'body') {
        this.bodyExample = null
        for (var key in this.actionData.parameters) {
          var field = this.actionData.parameters[key]
          if (field.in === 'body') {
            if (field.schema && field.schema.example) {
              this.bodyExample = field.schema.example
            }
          }
        }
      }
    }
  },
  components: {
    VueMarkdown,
    FormFields
  }
}



